@import "../../globalStyles/variables.scss";
@import "../../globalStyles/mixins.scss";

.benefits {
    display: flex;
    justify-content: center;
    background: transparent;
    z-index: 0;
    position: relative;
    min-height: 360px;

    &__container {
        height: auto;
        position: absolute;
        margin: 0 10px;
        padding: 10px 10px;
        display: flex;
        align-items: center;
        background: $headerBackground;
        background-image: url("../../../public/images/curiza.jpg");
        background-repeat: no-repeat;
        background-size: contain;
        background-position: bottom;
		bottom: -100px;

        &::after {
            @include pseudo;
            min-width: 100%;
            min-height: 100%;
            top: 0;
            left: 0;
            z-index: 1;
            background: linear-gradient(90deg, rgba(33, 31, 32, 1) 30%, rgba(255, 255, 255, 0) 100%);
        }
    }

    .descriptions {
        display: flex;
        justify-content: space-between;
        top: 0;
        left: 0;
        z-index: 3;

        &__button {
            width: 183px;
            height: 39px;
            background: rgba(255, 255, 255, 0.05);
            color: $primaryText;
            border: 1px solid rgba(255, 255, 255, 0.2);
            backdrop-filter: blur(2.5px);
            border-radius: 10px;
            text-transform: uppercase;
            padding-right: 15px;
            padding-left: 44px;
            font-style: normal;
            font-weight: 700;
            font-size: 10px;
            line-height: 16px;
            cursor: pointer;
            transition: all 1s ease;
        }

        &__button:hover {
            box-shadow: 0px 40px 50px rgba(0, 0, 0, 0.4);
            transition: all 1s ease;
            color: $tileTextColor;
        }
    }

    .description-block {
        color: $primaryText;
        font-family: "Ubuntu-Light";
        z-index: 3;
        width: 75%;
		top: 0;
        left: 0;
        display: none;

        &__title {
            font-style: normal;
            font-weight: 700;
            font-size: 12px;
            line-height: 39px;
            margin-bottom: 26px;
        }

        &__subtitle {
            font-style: normal;
            font-weight: 400;
            font-size: 12px;
            line-height: 25px;
            margin-bottom: 30px;
        }
    }

    &__buttons {
        display: grid;
        grid-template-columns: repeat(1, 71px);
        grid-template-rows: repeat(4, 71px);
        z-index: 2;
        top: 10px;
		right: 0;
        margin: 0;
        max-width: 40%;

        &>* {
            background: rgba(80, 75, 74, 0.9);
            backdrop-filter: blur(5px);
            border: 0;
            cursor: pointer;
        }
    }


    &__button {
        display: grid;
        gap: 10px;
        align-content: center;
        justify-items: center;
        width: 64px;
        height: 63px;
        transition: all 1s ease;
        font-family: "Ubuntu-Light";
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 25px;
        color: $primaryText;

        svg {
            transition: all 1s ease;
            fill: $primaryText;
            width: 40px;
            height: 40px;
        }

        span {
            display: none;
        }
    }


    .description-block.active {
        display: block;
    }

    &__button:hover {
        box-shadow: 0px 40px 50px rgba(0, 0, 0, 0.4);
        transition: all 1s ease;
        color: $tileTextColor;

        svg {
            transition: all 1s ease;
            fill: $tileTextColor;
        }
    }
}

@media screen and (min-width: 560px) {
    .benefits__container {
        background-size: cover;
    }
}

@media screen and (min-width: $tablet) {
    .benefits {
        height: 360px;

        &__container {
            min-width: 320px;
            height: 460px;
            margin: 0 30px;
            padding: 0 20px;
            background-size: contain;
            background-position: right;
        }

        .descriptions {
            &__button {
                width: 283px;
                height: 59px;
            }
        }

        .description-block.active {
            display: block;
        }

        .description-block {
            top: 0;
            max-width: 75%;

            &__title {
                font-size: 24px;
            }

            &__subtitle {
                font-size: 18px;
            }
        }

        &__buttons {
            right: 0;
            top: 10%;
            max-width: 25%;
        }

        span {
            display: none;
        }
    }
}

@media screen and (min-width: $desktop) {
    .benefits {
        height: 510px;

        &__container {
            max-width: 1363px;
            min-width: 320px;
            height: 610px;
            padding: 0 20px;
        }

        .description-block.active {
            display: block;
        }

        .description-block {
            max-width: 50%;

            &__title {
                font-size: 32px;
            }

            &__subtitle {
                font-size: 20px;
            }
        }

        &__buttons {
            grid-template-columns: repeat(2, 243px);
            grid-template-rows: repeat(2, 243px);
            max-width: 50%;
            top: 10%;
        }

        &__button {
            gap: 10px;
            width: 228px;
            height: 226px;
            font-size: 18px;

            span {
                display: block;
            }
        }
    }
}